/**
  Hamburger
**/
.hamburger {
  position: absolute;
  top: -1em;
  right: 2em;
  margin-left: -2em;
  margin-top: -45px;
  width: 2em;
  height: 45px;
  z-index: 1000;
}

.hamburger div {
  position: relative;
  width: 2em;
  height: 4px;
  border-radius: 3px;
  background-color: #f8982b;
  margin-top: 8px;
  transition: all 0.3s ease-in-out;
}

/**
Nav Styles
**/
.toggleButton{
  position: sticky;
  top:1em;
  right: 2em; 
}
.nav {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #250a3d;
  top: -100%; left: 0; right: 0; bottom: 0;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
  transform: scale(0);
  z-index: 999;
}
.nav-wrapper {
  position: fixed;
  overflow-y: auto;
  height: 100vh;
  width: 100vw;
  z-index: 9999;
  background-color: #250a3d;
  top:0;
  padding-top: 5em;
}
nav {
  text-align: left;
  margin-left: 10%;
  margin-top: 10%;
}
nav a {
  position: relative;
  text-decoration: none;
  color: #6ecddd !important;
  font-size: 2em;
  display: inline-block;
  margin-top: 1.25em;
  transition: color 0.2s ease-in-out;
  letter-spacing: 1px;
}
nav a:before {
  content: '';
  height: 0;
  position: absolute;
  width: 0.25em;
  background-color: white;
  left: -0.5em;
  transition: all 0.2s ease-in-out;
}
nav a:hover {
  color: white;
}
nav a:hover:before {
  height: 100%;
}

/**
Animations
**/
#toggle:checked + .hamburger .top-bun {
  transform: rotate(-45deg);
  margin-top: 25px;
}
#toggle:checked + .hamburger .bottom-bun {
  opacity: 0;
  transform: rotate(45deg);
}
#toggle:checked + .hamburger .meat {
  transform: rotate(45deg);
  margin-top: -7px;
}

#toggle:checked + .hamburger + .nav {
  top: 0;
  transform: scale(1);
}
